<template>
  <div class="app">
    <TheTopbar
      :isShowMenu="isShowMenu"
      @created="isMounted = true"
      @menu="isShowMenu = !isShowMenu"
    />

    <div
      v-if="isMounted"
      class="is-flex-auto is-flex"
    >
      <Loader delay="500" v-if="!isMounted"/>

      <MenuList
        v-else
        v-show="isShowMenu"
        :menus="menus"
        class="is-flex-auto"
        width="220px"
      />

      <router-view class="is-flex-auto" />
    </div>
  </div>
</template>

<script>
import MenuList from '@/components/Menus'

export default {
  name: 'Home',

  components: {
    MenuList,
    TheTopbar: () => import('./Topbar')
  },

  data () {
    return {
      isMounted: false,
      isShowMenu: false,
      menus: [
        {
          text: 'Code Library',
          icon: 'table',
          route: { name: 'code list' }
        },
        {
          text: 'Task Manage',
          icon: 'tasks',
          route: { name: 'task list' }
        }
      ]
    }
  },

  watch: {
    '$route.path': {
      immediate: true,
      handler () {
        if (this.$route.name === 'home') {
          this.$router.push({ name: 'task list' })
        }
      }
    }
  }
}
</script>

<style lang="sass">
.app
  width: 100vw
  height: 100vh
  display: flex
  flex-flow: column
</style>
